
import React, { Component } from 'react';
import { NavLink } from 'react-router-dom/cjs/react-router-dom.min';


class navigation extends Component {
    render() {
        return (
            <div className="handlerDiv">
                <div className="app_title">
                    <h1> Report Manager</h1>
                </div>

                <div className="nav">
                    <div className="handlerDiv">
                        <button>
                            <NavLink exact 
                                activeClassName="current" 
                                className="text" 
                                to="/hours">Hours
                            </NavLink>
                        </button>
                        <button>
                            <NavLink exact 
                                activeClassName="current" 
                                className="text" 
                                to="/overall">Overall View
                            </NavLink>
                        </button>
                        <button >
                            <NavLink exact 
                                activeClassName="current" 
                                className="text" 
                                to="/statics">Statics
                            </NavLink>
                        </button>
                    </div>
                    <div className="line"></div>
                </div>

                <div className="line"></div>
            </div>
                
        );
    }
}

export default navigation;