import React, { Component } from "react";
import Label from "./Label";
import TextInput from "./TextInput";
import TextArea from './TextArea'
import {up_item, down_item, cell_styles, Hour_Container_Styles, divideTwo, main_styles, text_style, btn_style, reduceDecimalPoint, delete_icon} from './styles'




const Cell = (props) => {
  return (
    <div style={cell_styles}>
      <div></div>
      <Label text={props.text} />
      <TextInput
        read={props.read}
        sendData={props.sendChildData}
        term={props.term}
        val={props.val}
      />
    </div>
  );
};


const GetHourAbbr = (props) => {
  let term;
  console.log(props.hour_no)
  if (Number(props.hour_no) === 1){
    term = "st"
  }
  else if (Number(props.hour_no) === 2){
    term = "nd"
  }
  else if (Number(props.hour_no) === 3){
    term = "rd"
  }
  else{
    term = "th"
  }
  return (
    <span>{props.hour_no}<sup>{term}</sup> Hour</span>
  );
};


class Hour extends Component {
 
  state = this.props.passValue

  findDefective = (ProductionQty, PassQty) => {
    let tmp = ProductionQty - PassQty
    return tmp;
  }

  findDHU = (ProductionQty, DefectsQty) => {
    let tmp = (DefectsQty / ProductionQty) * 100;
    console.log(reduceDecimalPoint(tmp, 2))
    return reduceDecimalPoint (tmp, 2)
  }

  findDefectivePercent = (ProductionQty, PassQty) => {
    let tmp = ((ProductionQty - PassQty) / ProductionQty) * 100;
    return reduceDecimalPoint(tmp, 2)
  }

  commonUpdate = () =>{
    let temp_dvq = this.findDefective(this.state.pq, this.state.psq);
    let temp_dqp = this.findDefectivePercent(this.state.pq, this.state.psq);
    let temp_dfq = this.findDHU(this.state.pq, this.state.dfq);

    if (!Number(temp_dvq)){
      temp_dvq = "-"
    }
    if (!Number(temp_dqp)){
      temp_dqp = "-"
    }
    if (!Number(temp_dfq)){
      temp_dfq = "-"
    }

    return {temp_dvq, temp_dqp, temp_dfq}
  }

  updateCell = () => {

    let {temp_dvq, temp_dqp, temp_dfq} = this.commonUpdate()

    this.props.sendData(this.state)
    this.setState({
        dhu: temp_dfq,
        dvq: temp_dvq,
        dqp: temp_dqp,
        collapse:false
      }, ()=>{
        this.updateState()
        let {temp_dvq, temp_dqp, temp_dfq}=this.commonUpdate()
        this.setState({
          dhu: temp_dfq,
          dvq: temp_dvq,
          dqp: temp_dqp,
          collapse:true
        })
    });
  }


  updateState = () => {
    if (this.state.dqp && this.state.dvq && this.state.dhu){
      this.props.sendData(this.state)
    }
  };

  handleHourCallback = (childData, term) => {
    if (term === "pq") {
      this.setState({ pq: childData },  this.updateCell);
    } else if (term === "psq") {
      this.setState({ psq: childData }, this.updateCell);
    } else if (term === "dfq") {
      this.setState({ dfq: childData },  this.updateCell);
    } else if (term === "dhu") {
      this.setState({ dhu: childData }, this.updateCell);
    } else if (term === "dvq") {
      this.setState({ dvq: childData }, this.updateCell);
    } else if (term === "dqp") {
      this.setState({ dqp: childData }, this.updateCell);
    } else if (term === "details") {
      this.setState({ details: childData }, this.updateCell);
    } else {
      console.log("No valid data found");
    }
  };

  handleCollapse = (e) => {
    if (this.state.collapse === false) {
      this.setState({ icon: 'down', collapse: true });
    } else {
      this.setState({ icon: 'up', collapse: false });
    }
    this.updateState()
  };

  deleteHour  = (e) => {
    this.props.removeHour(this.state.id)
  }


  render() {
    return (
      <div style={main_styles}>
        <div style={text_style}>
          <GetHourAbbr hour_no={this.props.hourNumber} />
          
          <button id="icon-btn" style={btn_style} onClick={this.handleCollapse}>
            {this.state.icon === 'up' ? up_item: down_item}
          </button>
          <button id="delete-btn" onClick={this.deleteHour}>          
              {delete_icon}
          </button>
        </div>


        {this.state.collapse ? (
          <div style={divideTwo} >
            <div key={this.props.cell_id} 
            style={Hour_Container_Styles}>
              <Cell
                text="Production "
                sendChildData={this.handleHourCallback}
                term="pq"
                val={this.state.pq}
              />
              <Cell
                text="Pass Qty"
                sendChildData={this.handleHourCallback}
                term="psq"
                val={this.state.psq}
              />
              <Cell
                text="Defects"
                sendChildData={this.handleHourCallback}
                term="dfq"
                val={this.state.dfq}
              />
              <Cell
                text="Defective"
                read={true}
                sendChildData={this.handleHourCallback}
                term="dvq"
                val={this.state.dvq}
              />
              <Cell
                text="DHU"
                read={true}
                sendChildData={this.handleHourCallback}
                term="dhu"
                val={this.state.dhu}
              />
              <Cell
                text="Defective (%)"
                read={true}
                sendChildData={this.handleHourCallback}
                term="dqp"
                val={this.state.dqp}
              />
            </div>
            <div id="textAreaDiv">
              <TextArea
                 label = {"Defects Code With Quantity"} 
                 text = { "(e.g) X27-54-LEG X28-7-BAR Others-2-Null ..." }
                 term = "details"
                 val={this.state.details}
                 sendData={this.handleHourCallback}
              />
            </div>
          </div>
        ) : (
          <span></span>
        )}
      </div>
    );
  }
}

export default Hour;
