import React from 'react';



const Label = (props) => {

    let styles = {
        display:"block",
        width:"100%",
        // backgroundColor:"#ececec", 
        backgroundColor:"transparent", 
        margin:"0px",
        padding: "2px",
        borderRadius: "5px",
        fontSize:"12px",
        color:"grey"
    }

    return (
        <span style={styles}>{props.text}</span>
    );
};

export default Label;