
import React from 'react';

let min_icon = (

<svg  width="16" height="16" fill="currentColor" className="min_icon"  viewBox="0 0 16 16">
  <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z"/>
  <path d="M3.544 10.705A.5.5 0 0 0 4 11h8a.5.5 0 0 0 .374-.832l-4-4.5a.5.5 0 0 0-.748 0l-4 4.5a.5.5 0 0 0-.082.537z"/>
</svg>
)

const OptionsMenu = (props) => {
    return (
        <div id= "pushOptions">
            <p>Date : {new Date().toLocaleDateString()}</p>
            <button className="add_hour_btn" onClick={()=>props.Add()}>Add Hour</button>
            <button id="submit_rep" onClick={()=>props.Clear()}>Clear All</button>
            <button className="min">{min_icon}</button>
        </div>
    );
};

export default OptionsMenu;



