let findTotalCalculation = (data) => {

  let result={
    totalPQ : 0,
    totalPSQ : 0,
    totalDFQ : 0,
    totalDVQ : 0,
    finalDQP : 0,
    finalDHU : 0,
    TotalHoursCount:0
  };

  let dividor =0
  // eslint-disable-next-line
  data.Hours.map((singleHour)=>{
    // eslint-disable-next-line
      singleHour.map((obj)=>{
        if (Number(obj)) {
          // do this
          // no tasks
        }
        else{
          if (!Number(obj.pq)){
            //pass
          }
          else{
            dividor += 1
          }
          result.totalPQ += Number(obj.pq) ? Number(obj.pq):0
          result.totalPSQ += Number(obj.psq) ? Number(obj.psq): 0
          result.totalDFQ += Number(obj.dfq) ?  Number(obj.dfq): 0
          result.totalDVQ += Number(obj.dvq) ?  Number(obj.dvq) : 0
          result.finalDQP += Number(obj.dqp) ? Number(obj.dqp)
          : 0
          result.finalDHU += Number(obj.dhu) ? Number(obj.dhu) : 0
        }
      })
  })

  // let dividor = data.Hours.length
  result.TotalHoursCount = dividor
 
  result.finalDHU = reduceDecimalPoint((result.finalDHU/dividor), 2)
  result.finalDQP = reduceDecimalPoint((result.finalDQP/dividor), 2)


  result.finalDHU =  Number( result.finalDHU ) ?  result.finalDHU : 0
  result.finalDQP =  Number( result.finalDQP ) ?  result.finalDQP : 0

  
  return result
}

  
let getDataForState = () => {

    let userData
  
    let ls = localStorage
  
    userData = JSON.parse(ls.getItem('receivedData'));
    // console.log("From Local Storage ...")
    // console.log(userData)
    if (userData){
      // console.log(userData.Hours)
      for (let i=0 ; i<userData.Hours.length; i++){
        userData.Hours[i][1].icon="up"
        userData.Hours[i][1].collapse=false
        console.log(userData.Hours[i][1].collapse)
      }
      userData.result = findTotalCalculation(userData)
      return userData
    }
    userData={
      num:0,
      Hours : [
      ],
    }
    userData.result = findTotalCalculation(userData)
    localStorage.setItem('receivedData',  JSON.stringify(userData));
    return userData
}

