import React, { Component } from "react";

class TextInput extends Component {
  state = {
    cellvalue: this.props.val,
    term: this.props.term,
  };

  handleChange = (e) => {
    let val = e.target.value;
    this.setState({ cellvalue: val });
  };

  onBlurEvent = (e) => {
    let val = e.target.value;
    this.setState({ cellvalue: val });
    this.props.sendData(val, this.state.term);
  };

  render() {
    return (
      <div>
        <input
          type="text"
          className={this.props.read ? "read" : "text_input"}
          name="cellvalue"
          id={`id_${Math.random() * 100000 + 1}`}
          value={this.state.cellvalue === 0 ? "" : this.state.cellvalue}
          onChange={this.handleChange}
          onBlur={this.onBlurEvent}
          readOnly={this.props.read}
          placeholder={this.props.read ? "Calculating" : "Enter Value"}
        />
      </div>
    );
  }
}

export default TextInput;
