
import Myrouter from './components/routing'
import Mynavigation from './components/navigation'
import { BrowserRouter } from 'react-router-dom/cjs/react-router-dom.min';




function App() {
  return (
    <div>
      <BrowserRouter>
        <Mynavigation/>
        <Myrouter/>
      </BrowserRouter>
    </div>
  );
}

export default App;
