import React from 'react';
import { NavLink } from 'react-router-dom/cjs/react-router-dom.min';
import logo from './rlogo.png' 



const index = () => {
    return (
        <React.Fragment>
             <div className="HomeContainer">
                <div className="rotate"></div>
                <p></p>
                <img className="rlogo" src={logo} alt="Logo"/>
                <h1>Report Manager</h1>
                <h3>version 1.0</h3>
                <div className="load" ><div id="status"></div></div>
            
                    <NavLink exact 
                        activeClassName="btnGo" 
                        className="btnGo" 
                        to="/hours">
                            Go
                    </NavLink> 
            </div>
            <div className="foot">
                <p>Developed With Passion  by <a href="https://www.shihab-portfolio.web.app">Shihabe Uddin </a></p>
            </div>
        </React.Fragment>
       
        
    );
};

export default index;