export let reduceDecimalPoint = (val, point) => {
    let str_val = String(val)
    let new_val = ""
    let countVal = str_val.length
    for (let i=0; i<countVal ; i++){
      new_val += str_val[i]
      if (str_val[i]==="."){
        let setPoint = point + 1
        countVal = i + setPoint
      }
    }
    let pf = parseFloat(new_val)
    console.log(pf)
    return pf
  }




