
import React, { Component } from 'react';


  
let getDataForState = () => {

    let userData
  
    let ls = localStorage
  
    userData = JSON.parse(ls.getItem('receivedData'));
    // console.log("From Local Storage ...")
    // console.log(userData)
    if (userData){
      // console.log(userData.Hours)
      for (let i=0 ; i<userData.Hours.length; i++){
        userData.Hours[i][1].icon="up"
        userData.Hours[i][1].collapse=false
        console.log(userData.Hours[i][1].collapse)
      }
      return userData
    }
    userData={
      num:0,
      Hours : [
      ],
    }
    localStorage.setItem('receivedData',  JSON.stringify(userData));
    return userData
}

let showDetailsAboutThis = (e) => {
    console.log("event")
    console.log(e)


    // console.log(term)
    // let x = 9;
}

const MakeBaseOfdefects = (dfc_str) => {

    let all_df = dfc_str
    console.log("Found ... "+all_df)
    all_df = all_df.split(" ") 

    return (
        <div>
            {
              all_df.map (sdf => {
                   return (
                       <span 
                            key={Math.random()}
                            className="df-base" 
                            onMouseOver={showDetailsAboutThis}
                        >
                        {sdf}
                        </span>
                   )
              })
            }
        </div>
    );
};



class index extends Component {

    state = getDataForState()

    render() {
        return (
            <div>
                <div className="HourContainer ovc">
                    <h1> Statics</h1>
                    <ul>
                    {
                        this.state.Hours.map((hour)=>{
                                let hourNum = hour[0];
                                let createNewListItem = true;
                                let hourDetails =  hour[1].details
                                console.log(hourDetails)
                                if (!hourDetails){
                                    createNewListItem = false
                                }
                                else if (hourDetails.length < 5){
                                    createNewListItem = false
                                }
                                
                                if (createNewListItem){
                                    return (
                                    <li key={Math.random()} >
                                        <span className='hrs'>Hour - {hourNum}</span> {MakeBaseOfdefects(hourDetails)}
                                    </li> )
                                }
                                return null
                        })
                    }
                    </ul>
                </div>
                <div className="footer">
                    <p>Copyright &copy; 2021 <a href="https://www.shihab-portfolio.web.app">Shihabe Uddin </a>. All Rights Reserved.</p>
                </div>
            </div>
        );
    }
}

export default index;

