import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom/cjs/react-router-dom.min';
import EntryHour from './pages/EntryHour'
import OverAllView from './pages/OverAllView'
import Statics from './pages/Statics'
import Home from './pages/Home'
import NotFound from './NotFound'


class routing extends Component {
    render() {
        return (
            <div>
                <Switch>
                    <Route exact path="/" component={Home} />
                    <Route exact path="/hours" component={EntryHour} />
                    <Route exact path="/overall" component={OverAllView} />
                    <Route exact path="/statics" component={Statics} />
                    <Route component={NotFound}></Route>
                </Switch>
            </div>
        );
    }
}

export default routing;