import React from 'react';
import { NavLink } from 'react-router-dom/cjs/react-router-dom.min';

const index=(props)=>{
        return (
            <div className="handlerDiv">
                <button>
                    <NavLink exact 
                        activeClassName="current" 
                        className="text" 
                        to="/hours">Hours
                    </NavLink>
                </button> 
                <button>
                    <NavLink exact 
                        activeClassName="current" 
                        className="text" 
                        to="/overall">Overall View
                    </NavLink>
                </button> 
                <button>
                    <NavLink exact 
                        activeClassName="current" 
                        className="text" 
                        to="/statics">Statics
                    </NavLink>
                </button>
            </div>
        );
    }

export default index;