import React, { Component } from 'react';
let styles = {
    display:"block",
    width:"100%",
    // backgroundColor:"#ececec", 
    backgroundColor:"transparent", 
    margin:"0px",
    padding: "5px",
    borderRadius: "5px",
    fontSize:"12px",
    color:"grey",
    paddingLeft: "20px"
}

class TextArea extends Component {
    state = {
        cellvalue: this.props.val,
        term: this.props.term,
    };

    handleChange = (e) => {
        let val = e.target.value;
        this.setState({ cellvalue: val });
    };

    onBlurEvent = (e) => {
        let val = e.target.value;
        this.setState({ cellvalue: val });
        this.props.sendData(val, this.state.term);
    };
    render() {
        return (
            <div>
                <span style={styles}>{this.props.label} </span>
                <textarea 
                    onBlur = {this.onBlurEvent}
                    onChange = {this.handleChange}
                    placeholder = {this.props.text}
                    value = {this.state.cellvalue ? this.state.cellvalue:""}
                />
            </div>
        );
    }
}

export default TextArea;